













































































































html {
    background-image: url(./assets/titanfall2.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    z-index:1;
    font-family:"Arial Black", Gadget, sans-serif;
    height: 100%;
    width: 100%;
}
body {
    margin:0;
    padding:0;
    height: 100%;
    width: 100%;
    background-color: rgba(0,0,0,0.3);
    z-index:100;
}
.logo {
    height: 110px;
    width: 600px;
    position: absolute;
    top: 20%;
    left: 10%;
}
.trailer {
    width: 560px;
    height: 315px;
    background-color: rgba(0,0,0,0.4);
    border-radius: 7px;
    padding: 5px 10px 5px 10px;
    position: absolute;
    top: 40%;
    left: 11%;
}
.clockStyle {
    width: 400px;
    height: 400px;
    background-color: rgba(0,0,0,0.3);
    border-radius: 7px;
    padding: 5px 10px 5px 10px;
    color:#0FF;
    font-size:36px;
    font-weight:bold;
    text-align: center;
    letter-spacing: 4px;
    position: absolute;
    top: 30%;
    left: 65%;
}
.adk-img {
    width: 75px;
    height: 75px;
    border-radius: 3px;
    position: absolute;
    bottom: 1%;
    left: 1%;
    z-index: 1000;
}
.adk-img-large {
    border-radius: 17px;
    position: relative;
    margin: 22px;
}
.adk-pop-up {
    background-color: rgba(0,0,0,0.9);
    border-radius: 17px;
    height: 300px;
    width: 300px;
    margin: 20px;
    position: absolute;
    bottom: 7%;
    left: 1%;
    z-index: 1500;
}
.titanfall-pop-up {
    width: 700px;
    background-color: rgba(0,0,0,0.3);
    border-radius: 7px;
    padding: 5px 10px 5px 10px;
    color:#fff;
    font-size:26px;
    font-weight:bold;
    text-align: center;
    letter-spacing: 4px;
    position: absolute;
    top: 15%;
    left: 7%;
    z-index: 2000;
}
.ease-it-transition {
    -webkit-transition: all .7s ease;
    transition: all .7s ease;
    overflow: hidden;
}
.ease-it-enter, .ease-it-leave {
    opacity: 0;
}
footer {
    background-color: rgba(0,0,0,0.4);
    color: #fff;
    font-size:20px;
    font-weight:bold;
    text-align: center;
    height: 100px;
    width: 100%;
    position: absolute;
    bottom: 0;
}
footer h1 {
    padding: 20px;
    margin:0;
}



